<template>
	<div class="cartconcontrol-box">
		<transition name="shopAdd">
			<div v-show="shops.count > 0">
			<transition name="rotateIn">
		         <svg class="icon icon-jian" aria-hidden="true" v-show="shops.count > 0" @click.stop="reduceShopsCount">
		            <use xlink:href="#icon-remove_circle_outlin"></use>
		        </svg>
		   </transition>
		   </div>
	   </transition>
	   	<transition name="fadeIn">
	    	<div class="goods-shop-count" v-show="shops.count > 0">{{shops.count}}</div>
	    </transition>
	    <div>
	    	<svg class="icon" aria-hidden="true" @click.stop="addShopsCount($event)">
	        	<use xlink:href="#icon-add_circle"></use>
	    	</svg>
	    </div>
	    
	</div>
	
</template>

<script>
	import Vue from 'vue'
	
	let SHOPSMAXLENGTH = 50;
	export default {
		data(){
			return{
				
			}
		},
		props:{
			shops:{
				type:Object,
				default:{}
			},
			nowShopsCount:{
				type:Number,
				default:0
			}
		},
		methods:{
			addShopsCount(event){
//				if(!event._constructed){
//					return
//				}
				if(this.nowShopsCount < SHOPSMAXLENGTH){
					if(!this.shops.count){
						Vue.set(this.shops,"count",1);
						this.$emit('cart-add',event.target);
						
					}else{
						this.shops.count++
						this.$emit('cart-add',event.target);
					}
				}else{
					this.$emit('ShopsChange',SHOPSMAXLENGTH);
				}
				
//					if(!this.shops.count){
//						Vue.set(this.shops,"count",1);
//						
//					}else{
//						this.shops.count++
//					}
					
					
			},
			reduceShopsCount(){
				if(this.shops.count == 0){
					return 
				}
				this.shops.count--
			},
		},
		computed:{
			
		}
	}
</script>

<style scoped>
	.cartconcontrol-box{
		display: flex;
		flex-direction: row;
		flex-wrap: nowrap;
		align-items: top;
	}
	.cartconcontrol-box svg{
		display: block;
		width: 0.42rem;
		height: 0.42rem;
		color: #00a0dc;
		background: #ffffff;
		border-radius: 0.21rem;
		z-index: 2;
	}
	.goods-shop-count{
		font-size: 0.2rem;
		color:rgb(147,153,159);
		font-weight: normal;
		width: 0.44rem;
		text-align: center;
		height: 0.42rem;
		line-height: 0.42rem;
		
	}
	.icon-jian{
		
	}
	.shopAdd-enter-active,.shopAdd-leave-active {
	  	transition: all 0.3s ease;
	}
	.shopAdd-enter,.shopAdd-leave-to{
		transform: translateX(0.3rem);
		opacity: 0;
	}
	.fadeIn-enter-active,.fadeIn-leave-active{
		transition: all 0.3s ease;
	}
	.fadeIn-enter,.fadeIn-leave-to{
		opacity: 0;
	}
	.rotateIn-enter-active,.rotateIn-leave-active{
		transition: all 0.3s ease;
	}
	.rotateIn-enter,.rotateIn-leave-to{
		transform: rotate(90deg);
	}
</style>


