<template>
	<div>
	<div class="seller-ratings-box" ref="sellerScroll">
		<div>
			<div class="seller-ratings-info">
				<div class="seller-ratings-info-left">
					<div class="seller-ratings-zhpf">{{seller.score}}</div>
					<div class="seller-ratings-text">综合评分</div>
					<div class="seller-ratings-fs">高于周边商家{{seller.rankRate}}%</div>
				</div>
				<div class="seller-ratings-info-right">
					<div>
						<span>服务态度</span>
						<div class="seller-ratings-star"><starcomponent :score="seller.serviceScore" :minstarstyle="true"></starcomponent></div>
						<span class="seller-fenshu">{{seller.serviceScore}}</span>
					</div>
					<div>
						<span>食物评分</span>
						<div class="seller-ratings-star"><starcomponent :score="seller.foodScore" :minstarstyle="true"></starcomponent></div>
						<span class="seller-fenshu">{{seller.foodScore}}</span>
					</div>
					<div><span>送达时间</span><div class="seller-ratings-deliveryTime">{{seller.deliveryTime}}分钟</div></div>
				</div>
			</div>
			<div class="seller-ratings-choose-hr"></div>
			<div class="seller-ratings-choose">
				<ratings :ratings="ratings" :selectText="selectText" :showContent="showContent" :selectType="selectType"  @selectBtnfu="selectBtn" @changeShowContentfu="changeShowContent"></ratings>
				<div class="seller-ratings-list">
					<ul>
						<li v-for="item in ratings" v-if="sellerRatingsJs(item.rateType,item.text)">
							<div class="seller-ratings-userimg">
								<img :src="item.avatar"/>
							</div>
							<div class="seller-ratings-content">
								<div class="seller-ratings-username">{{item.username}}</div>
								<div class="seller-ratings-userstar">
									<starcomponent :score="item.score" :userminstarstyle="true"></starcomponent>
									<div class="seller-ratings-deliverytime" v-if="item.deliveryTime">{{item.deliveryTime}}分钟送达</div>
								</div>
								<div class="seller-ratings-usertext">
									{{item.text}}
								</div>
								<div class="seller-ratings-shops" v-show="item.recommend != ''">
									<svg class="icon icon-nogood" aria-hidden="true" v-if="item.rateType == 1">
							            <use xlink:href="#icon-thumb_down"></use>
							        </svg>
									<svg class="icon icon-good" aria-hidden="true" v-if="item.rateType == 0">
							            <use xlink:href="#icon-thumb_up"></use>
							        </svg>
							        <div class="seller-ratings-shops-item" v-for="shops in item.recommend">
							        	{{shops}}
							        </div>
								</div>
							</div>
						</li>
					</ul>
				</div>
			</div>
		</div>
	</div>
	<shopCart :selectShops="selectShops" :seller="seller"></shopCart>
	</div>
</template>

<script>
	import shopCart from '../shopCart/shopCart'
	import BScroll from 'better-scroll'
	import starComponent from '../star/star'
	import ratings from '../ratings/ratings'
	const ERR_OK = 0;
	const ALL = 2;
	const TUIJIAN = 0;
	const BUTUIJIAN = 1;
	export default{
		props:{
			ratings:{
				type:Array,
				default(){
					return []
				}
			},
			seller:{
				type:Object,
				default(){
					return {}
				}
			},
			goods:{
				type:Array,
				default(){
					return []
				}
			}
			
		},
		data(){
			return {
				selectText:{
					allText:'全部',
					tjText:'满意',
					btjText:'不满意'
				},
				showContent:false,
				selectType:ALL,
			}
		},
		computed:{
			selectShops() {
				let shops = [];
				this.goods.forEach(function(good) {
					good.foods.forEach(function(selectShop) {
						if(selectShop.count) {
							shops.push(selectShop)
						}
					})
				})
				return shops;
			}
		},
		methods:{
			sellerRatingsJs(rateType,text){
				if(this.selectType == ALL || this.selectType == rateType){
					if(this.showContent && !text){
						return false;
					}else{
						return true;
					}
				}
			},
			selectBtn(index){
				this.selectType = index
			},
			changeShowContent(){
				this.showContent = !this.showContent;
			}
		},
		components:{
			'starcomponent':starComponent,
			ratings,
			shopCart,
		},
		created(){
			this.$nextTick(() => {
				this.sellerScroll = new BScroll(this.$refs.sellerScroll,{
					click:true,
				})
			})
		},
		watch:{
			selectType(){
				this.$nextTick(() => {
					return this.sellerScroll.refresh()
				})
			},
			showContent(){
				this.$nextTick(() => {
					return this.sellerScroll.refresh()
				})
			}
		}
	}
</script>

<style scoped>
	.seller-ratings-box{
		width: 100%;
		position: fixed;
		bottom: 1rem;
		top: 3.532rem;
		overflow: hidden;
		background: #ffffff;
	}
	.seller-ratings-info{
		display: flex;
		flex-flow: row nowrap;
		padding: 0.36rem 0.48rem;
		background: #ffffff;
		width: 100%;
		box-sizing: border-box;
		justify-content: space-between;
		border-bottom: 1px solid rgba(7,17,27,0.1);
	}
	@media screen and (-webkit-min-device-pixel-ratio:2) {
		.seller-ratings-info {
			border-bottom:0.5px solid rgba(7,17,27,0.1)
		}
	}
	
	@media screen and (-webkit-min-device-pixel-ratio:3) {
		.seller-ratings-info {
			border-bottom:0.3px solid rgba(7,17,27,0.1)
		}
	}
	.seller-ratings-info .seller-ratings-zhpf{
		font-size: 0.48rem;
		color: rgb(255,153,0);
		line-height: 0.56rem;
	}
	.seller-ratings-info .seller-ratings-info-left{
		text-align: center;
		padding-right: 0.44rem;
		border-right: 1px solid rgba(7,17,27,0.1);
	}
	@media screen and (-webkit-min-device-pixel-ratio:2) {
		.seller-ratings-info .seller-ratings-info-left {
			border-right: 0.5px solid rgba(7,17,27,0.1);
		}
	}
	
	@media screen and (-webkit-min-device-pixel-ratio:3) {
		.seller-ratings-info .seller-ratings-info-left {
			border-right: 0.3px solid rgba(7,17,27,0.1);
		}
	}
	.seller-ratings-text{
		font-size: 0.24rem;
		color: rgb(7,17,27);
		line-height: 0.24rem;
		margin:0.12rem 0 0.16rem 0;
	}
	.seller-ratings-fs{
		font-size: 0.2rem;
		color:#666666;
		line-height: 0.2rem;
		padding-bottom: 0.12rem;
	}
	.seller-ratings-info .seller-ratings-info-right{
		font-size: 0.24rem;
		color: rgb(7,17,27);
		line-height: 0.36rem;
	}
	.seller-ratings-info-right div{
		display: flex;
		flex-direction: row;
		flex-wrap: nowrap;
		justify-content: flex-start;
	}
	.seller-ratings-star{
		margin: 0.03rem 0.2rem 0 0.2rem;
	}
	.seller-ratings-info-right>div:nth-child(2){
		margin: 0.16rem 0;
	}
	.seller-fenshu{
		font-size: 0.24rem;
		color: rgb(255,153,0);
	}
	.seller-ratings-deliveryTime{
		margin: 0 0.26rem;
		color: 0.24rem;
		color: rgb(147,153,159);
	}
	.seller-ratings-choose{
		background: #ffffff;
		border-top: 0.5px solid rgba(7,17,27,0.1);
		padding-top: 0.36rem;
	}
	.seller-ratings-choose-hr{
		height: 0.36rem;
		width: 100%;
		background: #f3f5f7;
	}
	@media screen and (-webkit-min-device-pixel-ratio:2) {
		.seller-ratings-choose {
			border-top:0.5px solid rgba(7,17,27,0.1)
		}
	}
	
	@media screen and (-webkit-min-device-pixel-ratio:3) {
		.seller-ratings-choose {
			border-top:0.3px solid rgba(7,17,27,0.1)
		}
	}
	.seller-ratings-list{
		
	}
	.seller-ratings-list ul{
		padding: 0 0.36rem;
		
	}
	.seller-ratings-list ul li{
		display: flex;
		flex-flow: row nowrap;
		align-items: top;
		padding: 0.36rem 0;
		border-bottom: 1px solid rgba(7,17,27,0.1);
	}
	@media screen and (-webkit-min-device-pixel-ratio:2) {
		.seller-ratings-list ul li {
			border-bottom:0.5px solid rgba(7,17,27,0.1)
		}
	}
	
	@media screen and (-webkit-min-device-pixel-ratio:3) {
		.seller-ratings-list ul li {
			border-bottom:0.3px solid rgba(7,17,27,0.1)
		}
	}
	.seller-ratings-list ul li:last-child{
		border: 0;
	}
	.seller-ratings-userimg{
		width: 0.56rem;
		height: 0.56rem;
		margin-right: 0.24rem;
	}
	.seller-ratings-userimg img{
		width: 0.56rem;
		height: 0.56rem;
		border-radius: 50%;
		display: block;
	}
	.seller-ratings-list .seller-ratings-content{
		flex-grow: 1;
	}
	.seller-ratings-content .seller-ratings-username{
		font-size:0.2rem;
		color: rgb(7,17,27);
		line-height: 0.24rem;
	}
	.seller-ratings-content .seller-ratings-usertext{
		font-size:0.24rem;
		font-weight: 200;
		color: rgb(7,17,27);
		line-height: 0.36rem;
	}
	.seller-ratings-userstar{
		display: flex;
		flex-flow: row nowrap;
		justify-content: left;
		margin-top: 0.08rem;
		margin-bottom: 0.12rem;
	}
	.seller-ratings-deliverytime{
		font-size:0.2rem;
		font-weight: 200;
		color: rgb(147,153,159);
		line-height: 0.24rem;
		margin-left: 0.12rem;
	}
	.seller-ratings-shops{
		display: flex;
		flex-flow: row wrap;
		align-items: top;
		margin-top: 0.16rem;
	}
	.seller-ratings-shops svg{
		width: 0.24rem;
		height: 0.24rem;
		margin-right: 0rem;
		display: block;
		margin-top: 0.04rem;
		margin-right: 0.16rem;
	}
	.seller-ratings-shops svg.icon-good{
		color: rgb(0,160,220);
	}
	.seller-ratings-shops svg.icon-nogood{
		color: rgb(147,153,159);
	}
	.seller-ratings-shops-item{
		font-size:0.18rem;
		color: rgb(147,153,159);
		line-height: 0.32rem;
		border:1px solid rgba(7,17,27,0.1);
		border-radius: 0.02rem;
		height: 0.28rem;
		line-height: 0.32rem;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		text-align: center;
		max-width: 1.25rem;
		padding: 0 0.12rem;
		margin-right: 0.16rem;
		margin-bottom: 0.16rem;
	}
	@media screen and (-webkit-min-device-pixel-ratio:2) {
		.seller-ratings-shops-item {
			border:0.5px solid rgba(7,17,27,0.1);
		}
	}
	
	@media screen and (-webkit-min-device-pixel-ratio:3) {
		.seller-ratings-shops-item {
			border:0.3px solid rgba(7,17,27,0.1);
		}
	}
</style>


