import Vue from 'vue'
import VueRouter from 'vue-router'
import VueFontIcon from '../components/icon'
import VueGoods from '../components/goods/goods'
import sellerRatings from '../components/sellerRatings/sellerRatings'
import sellerInfo from '../components/sellerInfo/sellerInfo'
Vue.use(VueRouter)

const indexRouter = new VueRouter({
		routes:[
			{
				path:'/goods',
				component:VueGoods
			},
			{
				path:'/ratings',
				component:sellerRatings,
			},
			{
				path:'/seller',
				component:sellerInfo,
			},
			{
				path:'/icon',
				component:VueFontIcon
			}
		]
})
indexRouter.push('/goods');

//indexRouter.beforeEach(function(to,from,next){
//		if(to.path == '/'){
//			next({
//				path:'/goods',
//			})
//		}else{
//			next()
//		}
//})
export default indexRouter



