<template>
	<div>
		<div class="goods-box">
			<div class="goods-menu-list" ref="goodMenuList">
				<ul>
					<li v-for="(item,index) in goods" :class="{'active':currentHeightIndex === index}" @click="findShop(index)">
						<div>
							<div class="goods-menu-list-icon" v-show="item.type > 0" :class="supportsIcon[item.type]"></div><span>{{item.name}}</span></div>
					</li>
				</ul>
			</div>
			<div class="goods-shop-list" ref="goodShopList">
				<ul>
					<li v-for="item in goods" class="good-shop-hook">
						<div class="goods-shop-list-title">{{item.name}}</div>
						<div class="goods-shop-list-cont">
							<div class="goods-shop-list-cont-item" v-for="shop in item.foods" @click="lookFoodsInfo(shop)">
								<div class="goods-shop-list-img">
									<img :src="shop.icon" />
								</div>
								<div class="goods-shop-list-info">
									<div class="name">{{shop.name}}</div>
									<div class="description" v-if="shop.description">{{shop.description}}</div>
									<div class="description"><span>月售{{shop.sellCount}}份</span><span>好评率{{shop.rating}}%</span></div>
									<div class="price">
										<span class="new-price">{{shop.price}}</span><span v-if="shop.oldPrice">{{shop.oldPrice}}</span>
										<div class="goods-shop-change">
											<cartconcontrol :shops="shop" :nowShopsCount="nowShopsCount" @ShopsChange="errorShopsChange" @cart-add="balltarget"></cartconcontrol>
										</div>
									</div>
								</div>
							</div>
						</div>
					</li>
				</ul>
			</div>
			<transition name="errorfadeIn">
				<div class="error-shops-more" v-show="errorShopsStatus">
					购物车最多只能加入{{maxShops}}件商品
				</div>
			</transition>
		</div>
		<shop-cart :selectShops="selectShops" :seller="seller" @postNowShopsCount="ceshi" ref="shopcart"></shop-cart>
		<foods :foods="selectedFoods" ref="food" @foodsBall="balltarget" :nowShopsCount="nowShopsCount" @ShopsChange="errorShopsChange"></foods>
	</div>
</template>

<script>
	import BScroll from 'better-scroll'
	import shopCart from '../shopCart/shopCart'
	import cartconcontrol from '../cartconcontrol/cartconcontrol'
	import foods from '../foods/foods'
	const ERR_OK = 0;
	export default {
		props: {
			seller: {
				type: Object,
				default: {},
			},
			goods:{
				type: Array,
				default(){
					return [];
				}
			}
		},
		data() {
			return {
				
				shopHeightList: [],
				scrollY: 0,
				nowShopsCount: null,
				errorShopsStatus: false,
				maxShops: null,
				selectedFoods:{}
			}
		},
		methods: {
			_initScroll: function() {
				this.menuScroll = new BScroll(this.$refs.goodMenuList, {
					click: true,
				})
				this.ShopScroll = new BScroll(this.$refs.goodShopList, {
					swipeTime: 1600,
					probeType: 3,
					click: true,
				})

				this.ShopScroll.on('scroll', (pos) => {
					this.scrollY = Math.abs(Math.round(pos.y));
				})
			},
			_caclulateHeight: function() {
				let shopHeightobj = this.$refs.goodShopList.getElementsByClassName('good-shop-hook');
				let shopHeight = 0;
				this.shopHeightList.push(shopHeight);
				for(let i = 0; i < shopHeightobj.length; i++) {
					shopHeight += shopHeightobj[i].clientHeight;
					this.shopHeightList.push(shopHeight);
				}
			},
			findShop: function(index, e) {
				if(!event._constructed) {
					return
				}
				let shopHeightobj = this.$refs.goodShopList.getElementsByClassName('good-shop-hook');
				this.ShopScroll.scrollToElement(shopHeightobj[index], 300)
			},
			ceshi: function(nowShopsCount) {
				this.nowShopsCount = nowShopsCount;
			},
			errorShopsChange: function(maxShops) {

				this.maxShops = maxShops
				this.errorShopsStatus = true;
				setTimeout(() => {
					this.errorShopsStatus = false;
				}, 2000);

			},
			balltarget: function(e) {
				this.$nextTick(() => {
					this.$refs.shopcart.drop(e);
				})
			},
			lookFoodsInfo:function(foods){
				this.selectedFoods = foods;
				this.$refs.food.foodsShow();
			}
		},
		created() {
			this.$nextTick(() => {
				this._initScroll()
				this._caclulateHeight()
			})
			this.supportsIcon = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];

		},
		computed: {
			currentHeightIndex() {
				for(let i = 0; i < this.shopHeightList.length; i++) {
					let height1 = this.shopHeightList[i];
					let height2 = this.shopHeightList[i + 1];
					if(!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
						return i
					}
				}
				return 0;
			},
			selectShops() {
				let shops = [];
				this.goods.forEach(function(good) {
					good.foods.forEach(function(selectShop) {
						if(selectShop.count) {
							shops.push(selectShop)
						}
					})
				})
				return shops;
			}
		},
		components: {
			'shop-cart': shopCart,
			'cartconcontrol': cartconcontrol,
			foods,
		},
		watch:{
			goods:function(){
//				this.$nextTick(() => {
//					this._initScroll()
//					this._caclulateHeight()
//				})
				this.$nextTick(() => {
					this.menuScroll.refresh()
					this.ShopScroll.refresh()
				})
			}
		}
	}
</script>

<style scoped>
	.goods-box {
		display: flex;
		position: fixed;
		width: 100%;
		top: 3.532rem;
		bottom: 1rem;
		flex-direction: row;
		flex-wrap: nowrap;
	}
	
	.goods-box .goods-menu-list {
		width: 1.6rem;
		flex-shrink: 0;
		overflow: hidden;
	}
	
	.goods-box .goods-shop-list {
		flex-grow: 1;
		overflow: hidden;
	}
	
	.goods-menu-list ul li {
		width: 100%;
		box-sizing: border-box;
		padding: 0.32rem 0.15rem;
		font-size: 0.24rem;
		color: #666666;
		line-height: 0.29rem;
		position: relative;
	}
	
	.goods-menu-list ul li:after {
		position: absolute;
		bottom: 0rem;
		border-bottom: 1px solid #ededed;
		width: 100%;
		content: '';
		left: 0;
	}
	
	@media screen and (-webkit-min-device-pixel-ratio:2) {
		.goods-menu-list ul li:after {
			border-bottom: 0.5px solid #ededed;
		}
	}
	
	@media screen and (-webkit-min-device-pixel-ratio:3) {
		.goods-menu-list ul li:after {
			border-bottom: 0.3px solid #ededed;
		}
	}
	
	.goods-shop-list-title {
		font-size: 0.26rem;
		color: #666;
		font-weight: 700;
		padding: 0.17rem 0.8rem 0.17rem 0.26rem;
		background-color: #f1f1f1;
		box-sizing: border-box;
	}
	
	.goods-shop-list-cont {
		padding: 0rem 0.26rem;
		background: #ffffff;
	}
	
	.goods-shop-list-cont-item {
		width: 100%;
		padding: 0.26rem 0;
		box-sizing: border-box;
		display: flex;
		flex-direction: row;
		flex-wrap: nowrap;
		border-bottom: 1px solid rgba(7, 17, 27, 0.1);
	}
	
	@media screen and (-webkit-min-device-pixel-ratio:2) {
		.goods-shop-list-cont-item {
			border-bottom: 0.5px solid rgba(7, 17, 27, 0.1);
		}
	}
	
	@media screen and (-webkit-min-device-pixel-ratio:3) {
		.goods-shop-list-cont-item {
			border-bottom: 0.3px solid rgba(7, 17, 27, 0.1);
		}
	}
	
	.goods-shop-list-cont-item:last-child {
		border: 0;
	}
	
	.goods-shop-list-img {
		width: 1.44rem;
		height: 1.44rem;
		flex-shrink: 0;
	}
	
	.goods-shop-list-img img {
		width: 1.44rem;
		height: 1.44rem;
		display: block;
		border-radius: .053333rem;
	}
	
	.goods-shop-list-info {
		flex-grow: 1;
		margin-left: 0.2rem;
		position: relative;
	}
	
	.goods-shop-list-info .name {
		font-size: 0.26rem;
		color: rgb(7, 17, 27);
		line-height: 0.28rem;
		margin-top: 0.04rem;
	}
	
	.goods-shop-list-info .description {
		font-size: 0.2rem;
		color: rgb(147, 153, 159);
		margin-top: 0.13rem;
		width: 3.75rem;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	
	.goods-shop-list-info .description span {
		margin-right: 0.24rem;
	}
	
	.goods-shop-list-info .price {
		width: 100%;
		font-size: 0.2rem;
		color: rgb(147, 153, 159);
		line-height: 0.48rem;
		font-weight: 700;
		position: absolute;
		bottom: -0.1rem;
	}
	
	.goods-shop-list-info .price .new-price {
		font-size: 0.28rem;
		color: #f01414;
		line-height: 0.48rem;
		font-weight: 700;
		margin-right: 0.16rem;
	}
	
	.goods-shop-list-info .price .new-price:before {
		content: "\A5";
		font-size: 0.2rem;
		margin-right: 0.02rem;
		vertical-align: 0.005rem;
	}
	
	.goods-shop-list-info .price span:nth-child(2) {
		text-decoration: line-through;
	}
	
	.goods-menu-list ul li.active {
		background: #ffffff;
		position: relative;
	}
	
	.goods-menu-list ul li.active:after {
		width: 3px;
		background-color: #2b8de7;
		position: absolute;
		left: 0;
		top: 0;
	}
	
	.goods-shop-change {
		position: absolute;
		right: 0;
		top: 0;
	}
	
	.error-shops-more {
		width: 66%;
		position: absolute;
		font-size: 0.26rem;
		background-color: rgba(0, 0, 0, 0.8);
		height: 0.6rem;
		line-height: 0.6rem;
		text-align: center;
		border-radius: ;
		color: #ffffff;
		border-radius: 0.3rem;
		left: 50%;
		margin-left: -33%;
		top: 0.6rem;
		z-index: 999;
	}
	
	.errorfadeIn-enter-active,
	.errorfadeIn-leave-active {
		transition: all 0.15s linear;
	}
	
	.errorfadeIn-enter,
	.errorfadeIn-leave-to {
		opacity: 0;
	}
	
	.goods-menu-list-icon {
		display: inline-block;
		vertical-align: middle;
		width: 0.24rem;
		height: 0.24rem;
		background-size: 0.24rem 0.24rem;
		margin-right: 0.05rem;
	}
	
	.goods-menu-list-icon.decrease {
		background-image: url('../../common/img/decrease_4@2x.png');
	}
	
	.goods-menu-list-icon.discount {
		background-image: url('../../common/img/discount_4@2x.png');
	}
	
	.goods-menu-list-icon.guarantee {
		background-image: url('../../common/img/guarantee_4@2x.png');
	}
	
	.goods-menu-list-icon.invoice {
		background-image: url('../../common/img/invoice_4@2x.png');
	}
	
	.goods-menu-list-icon.special {
		background-image: url('../../common/img/special_4@2x.png');
	}
	
	.goods-menu-list ul li span {
		vertical-align: middle;
	}
</style>


