<template>
	<div>
		<div class="sellerinfo-box" ref="sellerInfoScroll">
			<div>
			<div class="sellerinfo-cont">
				<div class="sellerinfo">
					<div class="sellerinfo-left">
						<div class="sellerinfo-name">{{seller.name}}</div>
						<div class="sellerinfo-star">
							<div><starComponent :minstarstyle="true" :score='seller.score'></starComponent></div>
							<div class="text">(661)</div>
							<div class="text">月售{{seller.sellCount}}单</div>
						</div>
					</div>
					<div class="sellerinfo-like">
						<div>
							<svg class="icon" aria-hidden="true">
					            <use xlink:href="#icon-favorite"></use>
					        </svg>
				        </div>
				        <div class="text">收藏</div>
					</div>
				</div>
					<div class="seller-bottom">
						<div class="seller-bottom-item">
							<div class="title">起送价</div>
							<div>{{seller.minPrice}}<span>元</span></div>
						</div>
						<div class="seller-bottom-item">
							<div class="title">商家配送</div>
							<div>{{seller.deliveryPrice}}<span>元</span></div>
						</div>
						<div class="seller-bottom-item">
							<div class="title">平均配送时间</div>
							<div>{{seller.deliveryTime}}<span>元</span></div>
						</div>
					</div>
				</div>
				<div class="seller-ratings-choose-hr"></div>
				<div class="seller-ratings-notice">
					<div class="seller-ratings-notice-title">公告与活动</div>
					<div class="seller-ratings-notice-cont">
						{{seller.bulletin}}
					</div>
					<div class="seller-ratings-activity">
					<ul>
						<li v-for="item in seller.supports">
							<div class="header-info-brand" :class="supportsIcon[item.type]"></div>
							<div>{{item.description}}</div>
						</li>
					</ul>
					</div>
				</div>
				<div class="seller-ratings-choose-hr"></div>
				<div class="seller-ratings-notice seller-pics">
					<div class="seller-ratings-notice-title">商家实景</div>
					<div ref="sellerPic">
						<ul :style="picwidth">
							<li v-for="item in seller.pics">
								<img :src="item" />
							</li>
						</ul>
					</div>
				</div>
				<div class="seller-ratings-choose-hr"></div>
				<div class="seller-ratings-notice">
					<div class="seller-ratings-notice-title">商家信息</div>
					<div class="seller-address">
						<ul>
							<li v-for="item in seller.infos">
								{{item}}
							</li>
						</ul>
					</div>
				</div>
			</div>
		</div>
		<shopCart :selectShops="selectShops" :seller="seller"></shopCart>
	</div>
</template>

<script>
	import BScroll from 'better-scroll'
	import starComponent from '../star/star'
	import shopCart from '../shopCart/shopCart'
	export default {
		props:{
			seller:{
				type:Object,
				default(){
					return {}
				}
			},
			goods:{
				type:Array,
				default(){
					return []
				}
			}
		},
		data(){
			return{
				picwidth : {
					width:this.seller.pics.length * 2.52  + 'rem'
				},
			}
		},
		components:{
			starComponent,
			shopCart,
		},
		methods:{
			
		},
		created(){
			this.$nextTick(() => {
				this.sellerInfoScroll = new BScroll(this.$refs.sellerInfoScroll,{
					click:true,
				})
				this.sellerPic = new BScroll(this.$refs.sellerPic,{
					click:true,
					scrollX:true,
					scrollY:false,
				})
			})
			this.supportsIcon = ['decrease','discount','special','invoice','guarantee'];
		},
		computed:{
			selectShops() {
				let shops = [];
				this.goods.forEach(function(good) {
					good.foods.forEach(function(selectShop) {
						if(selectShop.count) {
							shops.push(selectShop)
						}
					})
				})
				return shops;
			}
		}
	}
</script>

<style>
	.sellerinfo-box{
		width: 100%;
	    position: fixed;
	    bottom: 1rem;
	    top: 3.532rem;
	    overflow: hidden;
	    background: #ffffff;
	}
	.sellerinfo{
		padding: 0 0 0.36rem 0;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
		border-bottom: 1px solid rgba(7,17,27,0.1);
	}
	@media screen and (-webkit-min-device-pixel-ratio:2) {
		.sellerinfo {
			border-bottom:0.5px solid rgba(7,17,27,0.1)
		}
	}
	
	@media screen and (-webkit-min-device-pixel-ratio:3) {
		.sellerinfo {
			border-bottom:0.3px solid rgba(7,17,27,0.1)
		}
	}
	.sellerinfo-name{
		font-size:0.28rem;
		color: rgb(7,17,27);
		line-height: 0.28rem;
	}
	.sellerinfo-star{
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		margin-top: 0.12rem;
	}
	.sellerinfo-star .text{
		font-size:0.2rem;
		color: rgb(77,85,93);
		margin-left: 0.16rem;
		margin-right: 0.08rem;
	}
	.sellerinfo-cont{
		padding: 0.36rem 0.36rem 0 0.36rem;
		border-bottom: 1px solid #eee;
	}
	@media screen and (-webkit-min-device-pixel-ratio:2) {
		.sellerinfo-cont {
			border-bottom:0.5px solid #eee;
		}
	}
	
	@media screen and (-webkit-min-device-pixel-ratio:3) {
		.sellerinfo-cont {
			border-bottom:0.3px solid #eee;
		}
	}
	.sellerinfo-like{
		text-align: center;
		flex-shrink: 0;
		color: rgb(77,85,93);
		font-size: 0.2rem;
	}
	.sellerinfo-like svg{
		width: 0.48rem;
		height: 0.48rem;
		color: rgb(240,20,20);
	}
	.sellerinfo-like .text{
		color: rgb(77,85,93);
		font-size: 0.2rem;
	}
	.seller-bottom {
		display: flex;
		flex-flow: row nowrap;
	}
	.seller-bottom .seller-bottom-item{
		margin: 0.36rem 0;
		flex-grow: 1;
		font-size:0.48rem;
		color: rgb(7,17,27);
		font-weight: 200;
		text-align: center;
		border-right: 1px solid rgba(7,17,27,0.1);
	}
	@media screen and (-webkit-min-device-pixel-ratio:2) {
		.seller-bottom .seller-bottom-item {
			border-right:0.5px solid rgba(7,17,27,0.1);
		}
	}
	
	@media screen and (-webkit-min-device-pixel-ratio:3) {
		.seller-bottom .seller-bottom-item {
			border-right:0.3px solid rgba(7,17,27,0.1);
		}
	}
	.seller-bottom .seller-bottom-item:last-child{
		border: 0;
	}
	.seller-bottom .seller-bottom-item span{
		font-size:0.2rem;
		
	}
	.seller-bottom .seller-bottom-item .title{
		font-size:0.2rem;
		color: rgb(147,153,159);
		margin-bottom: 0.08rem;
		font-weight: normal;
	}
	.seller-ratings-choose-hr{
		height: 0.36rem;
		width: 100%;
		background: #f3f5f7;
	}
	.seller-ratings-notice{
		padding: 0.3rem 0.3rem 0 0.3rem;
	}
	.seller-ratings-notice-title{
		font-size:0.28rem;
		color: rgb(7,17,27);
		margin-bottom: 0.16rem;
	}
	.seller-ratings-notice-cont{
		font-size:0.24rem;
		font-weight: 200;
		color: rgb(240,20,20);
		line-height: 0.48rem;
		padding: 0 0.24rem;
	}
	.seller-ratings-activity{
		margin-top: 0.32rem;
	}
	.seller-ratings-activity ul li{
		font-size:0.24rem;
		font-weight: 200;
		color: rgb(7,17,27);
		padding: 0.32rem 0;
		border-top:1px solid rgba(7,17,27,0.1);
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
	}
	@media screen and (-webkit-min-device-pixel-ratio:2) {
		.seller-ratings-activity ul li {
			border-top:0.5px solid rgba(7,17,27,0.1);
		}
	}
	
	@media screen and (-webkit-min-device-pixel-ratio:3) {
		.seller-ratings-activity ul li {
			border-top:0.3px solid rgba(7,17,27,0.1);
		}
	}
	.seller-ratings-activity ul li:last-child{
		border-bottom:1px solid rgba(7,17,27,0.1);
	}
	@media screen and (-webkit-min-device-pixel-ratio:2) {
		.seller-ratings-activity ul li:last-child{
			border-bottom:0.5px solid rgba(7,17,27,0.1);
		}
	}
	
	@media screen and (-webkit-min-device-pixel-ratio:3) {
		.seller-ratings-activity ul li:last-child {
			border-bottom:0.3px solid rgba(7,17,27,0.1);
		}
	}
	.header-info-brand{
		width: 0.32rem;
		height: 0.32rem;
		background-size: 0.32rem 0.32rem;
		margin-right: 0.12rem;
	}
	.header-info-brand.decrease{
		background-image: url('../../common/img/decrease_4@2x.png');
	}
	.header-info-brand.discount{
		background-image: url('../../common/img/discount_4@2x.png');
	}
	.header-info-brand.guarantee{
		background-image: url('../../common/img/guarantee_4@2x.png');
	}
	.header-info-brand.invoice{
		background-image: url('../../common/img/invoice_4@2x.png');
	}
	.header-info-brand.special{
		background-image: url('../../common/img/special_4@2x.png');
	}
	.seller-ratings-notice.seller-pics ul{
		padding:0 0rem 0.36rem 0rem;
		display: flex;
		flex-flow: row nowrap;
	}
	.seller-ratings-notice.seller-pics ul li{
		margin-right: 0.12rem;
		width: 2.4rem;
		height: 1.8rem;
	}
	.seller-ratings-notice.seller-pics ul li img{
		width: 2.4rem;
		height: 1.8rem;
	}
	.seller-ratings-notice{
		border-top: 1px solid #eee;
		border-bottom: 1px solid #eee;
	}
	@media screen and (-webkit-min-device-pixel-ratio:2) {
		.seller-ratings-notice{
			border-top: 0.5px solid #eee;
			border-bottom: 0.5px solid #eee;
		}
	}
	
	@media screen and (-webkit-min-device-pixel-ratio:3) {
		.seller-ratings-notice {
			border-top: 0.3px solid #eee;
			border-bottom: 0.3px solid #eee;
		}
	}
	.seller-address ul li {
		border-top: 1px solid rgb(7,17,27,0.1);
		padding: 0.32rem 0.24rem;
		font-size:0.24rem;
		font-weight: 200;
		line-height: 0.32rem;
	}
	@media screen and (-webkit-min-device-pixel-ratio:2) {
		.seller-address ul li {
			border-top:0.5px solid rgba(7,17,27,0.1);
		}
	}
	
	@media screen and (-webkit-min-device-pixel-ratio:3) {
		.seller-address ul li {
			border-top:0.3px solid rgba(7,17,27,0.1);
		}
	}
	.seller-address ul li:last-child{
		border-bottom:1px solid rgba(7,17,27,0.1);
	}
	@media screen and (-webkit-min-device-pixel-ratio:2) {
		.seller-address ul li:last-child{
			border-bottom:0.5px solid rgba(7,17,27,0.1);
		}
	}
	
	@media screen and (-webkit-min-device-pixel-ratio:3) {
		.seller-address ul li:last-child {
			border-bottom:0.3px solid rgba(7,17,27,0.1);
		}
	}
</style>


