<template>
	<transition name="foodanimate">
		<div class="foods-box" v-show="foodsFlag" ref="foodsbox">
			<div class="foods-scroll">
				<div class="foods-bg">
					<img :src="foods.image" />
					<div class="foods-close" @click="foodHide">
						<svg class="icon" aria-hidden="true">
				            <use xlink:href="#icon-keyboard_arrow_right"></use>
				        </svg>
					</div>
				</div>
				<div class="foods-info">
					<div class="foods-info-left">
						<div class="foods-name">{{foods.name}}</div>
						<div class="foods-rating"><span>月售{{foods.sellCount}}份</span><span>好评率{{foods.rating}}%</span></div>
						<div class="foods-price">￥<span>{{foods.price}}</span><span v-if="foods.oldPrice">￥{{foods.oldPrice}}</span></div>
					</div>
					<div class="foods-info-right">
						<div class="add-shop-cart" v-show="!foods.count" @click="addShopsCart(foods)">
							加入购物车
						</div>
						<cartconcontrol :shops="foods" v-show="foods.count" @cart-add="balltarget" :nowShopsCount="nowShopsCount"  @ShopsChange="errorShopsChange"></cartconcontrol>
					</div>
				</div>
				<transition name="errorfadeIn">
					<div class="error-shops-more" v-show="errorShopsStatus">
						购物车最多只能加入{{maxShops}}件商品
					</div>
				</transition>
				<div class="foods-info-js" v-if="foods.info">
					<div class="foods-info-js-title">商品介绍</div>
					<div class="foods-info-js-cont">{{foods.info}}</div>
				</div>
				<div class="foods-ratings">
					<div class="foods-ratings-title">
						商品评价
					</div>
					<ratings :ratings="foods.ratings" :showContent="showContent" :selectType="selectType" :selectText="selectText" @selectBtnfu="selectBtn" @changeShowContentfu="changeShowContent"></ratings>
					<div class="ratings-cont-list">
						<ul v-if="foods.ratings && foods.ratings.length > 0">
							<li v-for="item in foods.ratings" v-show="newShow(item.rateType,item.text)">
								<div class="ratings-cont-item-top">
									<div class="ratings-cont-item-time">{{item.rateTime | formatDate}}</div>
									<div class="ratings-cont-item-user-info">
										<div class="ratings-user-name">
											<span>{{item.username}}</span>
											<img :src="item.avatar">
										</div>
									</div>
								</div>
								<div class="ratings-usr-text">
									 <svg class="icon icon-nogood" aria-hidden="true" v-show="item.rateType == 1">
							            <use xlink:href="#icon-thumb_down"></use>
							        </svg>
									<svg class="icon icon-good" aria-hidden="true" v-show="item.rateType == 0">
							            <use xlink:href="#icon-thumb_up"></use>
							        </svg>
									<span>{{item.text?item.text:"用户暂无评论"}}</span>
								</div>
							</li>
						</ul>
						<ul v-if="!foods.ratings || foods.ratings.length == 0">
							<li>
								<div class="ratings-usr-text">
									暂无评论
								</div>
							</li>
						</ul>
					</div>
				</div>
			</div>
		</div>
	</transition>
</template>
<script>
	import BScroll from 'better-scroll'
	import cartconcontrol from '../cartconcontrol/cartconcontrol'
	import {formatDate} from '../../common/js/date.js'
	import ratings from '../ratings/ratings'
	import Vue from 'vue'
	const ALL = 2;
	const TUIJIAN = 0;
	const BUTUIJIAN = 1;
	
	export default{
		props:{
			foods:{
				type:Object,
				default:{}
			},
			nowShopsCount:{
				type:Number,
				default:0,
			}
		},
		created(){
			
			
		},
		data(){
			return{
				foodsStatus:false,
				foodsFlag:false,
				maxShops:0,
				errorShopsStatus:false,
				showContent:false,
				selectType:ALL,
				selectText:{
					allText:'全部',
					tjText:'推荐',
					btjText:'吐槽'
				}
			}
		},
		methods:{
			foodsShow(){
				this.showContent = false;
				this.selectType = ALL;
			 	this.foodsFlag = true;
			 	this.$nextTick(() => {
					this.foodsBox = new BScroll(this.$refs.foodsbox,{
						click:true,
					})
				})
			},
			foodHide(){
				this.foodsFlag = false;	
			},
			addShopsCart(){
				if(!this.foods.count){
					Vue.set(this.foods,'count',1);
				}
			},
			balltarget: function(e) {
				this.$emit('foodsBall',e);
			},
			errorShopsChange: function(maxShops) {
				this.maxShops = maxShops
				this.errorShopsStatus = true;
				setTimeout(() => {
					this.errorShopsStatus = false;
				}, 2000);

			},
			selectBtn(index){
				this.selectType = index
			},
			changeShowContent(){
				this.showContent = !this.showContent;
			},
			newShow(rateType,text){
//				if(this.showContent && !text){
//					return false;
//				}
				if(rateType == this.selectType || this.selectType == ALL){
					if(this.showContent && !text){
						return false;
					}else{
						return true
					}
				}
			}
		},
		components:{
			'cartconcontrol': cartconcontrol,
			ratings,
		},
		watch:{
			selectType(){
				this.$nextTick(() => {
					return this.foodsBox.refresh()
				})
			},
			showContent(){
				this.$nextTick(() => {
					return this.foodsBox.refresh()
				})
			}
		},
		filters:{
			formatDate(time){
				let date = new Date(time);
				return formatDate(date,'yyyy-MM-dd hh:mm');
			}
		}
	}
</script>

<style scoped>
	.foods-box{
		position:fixed;
		top:0;
		width: 100%;
		bottom: 1rem;
		background:#f3f5f7;
		overflow: hidden;
	}
	.foods-bg{
		width: 100%;
		height: 7.5rem;
		position: relative;
	}
	.foods-bg img{
		width: 100%;
		height: 100%;
		display: block;
	}
	.foods-bg .foods-close{
		position: absolute;
		top: 0;
		left: 0;
	}
	.foods-bg .foods-close svg{
		color: #FFFFFF;
		width: 0.6rem;
		height: 0.9rem;
		transform: rotate(180deg);
		display: block;
	}
	.foodanimate-enter-active,.foodanimate-leave-active{
		transition: all 0.3s linear;
	}
	.foodanimate-enter,.foodanimate-leave-to{
		transform: translate3d(100%,0,0);
	}
	.foods-info{
		display: flex;
		flex-direction: row;
		flex-wrap: nowrap;
		padding: 0.36rem;
		border-bottom:1px solid rgba(7,17,27,0.1);
		background: #ffffff;
		justify-content: space-between;
		align-items: flex-end;
		margin-bottom: 0.32rem;
	}
	@media screen and (-webkit-min-device-pixel-ratio:2) {
		.foods-info {
			border-bottom:0.5px solid rgba(7,17,27,0.1)
		}
	}
	
	@media screen and (-webkit-min-device-pixel-ratio:3) {
		.foods-info {
			border-bottom:0.3px solid rgba(7,17,27,0.1)
		}
	}
	.foods-info-left .foods-name{
		font-size: 0.28rem;
		font-weight: 700;
		color: rgb(7,17,27);
		line-height: 0.28rem;
	}
	.foods-info-left .foods-rating{
		font-size: 0.2rem;
		color: rgb(147,153,159);
		line-height: 0.48rem;
		margin-top: 0.16rem 0 0.36rem 0;
	}
	.foods-info-left .foods-rating span{
		margin-right: 0.24rem;
	}
	.foods-info-left .foods-price{
		font-size: 0.2rem;
		font-weight: 700;
		color: rgb(240,20,20);
		line-height: 0.48rem;
	}
	.foods-info-left .foods-price span:first-child{
		font-size: 0.28rem;
		margin-right: 0.24rem;
	}
	.foods-info-left .foods-price span:nth-child(2){
		color: rgb(147,153,159) !important;
		text-decoration: line-through;
	}
	.add-shop-cart{
		width: 1.48rem;
		height: 0.48rem;
		font-size: 0.2rem;
		color: rgb(255,255,255);
		border-radius: 0.24rem;
		background-color: rgb(0,160,220);
		text-align: center;
		line-height: 0.48rem;
	}
	.error-shops-more {
		width: 66%;
		position: fixed;
		font-size: 0.26rem;
		background-color: rgba(0, 0, 0, 0.8);
		height: 0.6rem;
		line-height: 0.6rem;
		text-align: center;
		border-radius: ;
		color: #ffffff;
		border-radius: 0.3rem;
		left: 50%;
		margin-left: -33%;
		top: 30%;
		z-index: 999;
	}
	
	.errorfadeIn-enter-active,
	.errorfadeIn-leave-active {
		transition: all 0.15s linear;
	}
	
	.errorfadeIn-enter,
	.errorfadeIn-leave-to {
		opacity: 0;
	}
	.foods-info-js{
		margin-bottom: 0.32rem;
		background: #ffffff;
		padding: 0.36rem;
		border-bottom:1px solid rgba(7,17,27,0.1);
		border-top:1px solid rgba(7,17,27,0.1)
	}
	@media screen and (-webkit-min-device-pixel-ratio:2) {
		.foods-info-js {
			border-bottom:0.5px solid rgba(7,17,27,0.1);
			border-top:0.5px solid rgba(7,17,27,0.1)
		}
	}
	
	@media screen and (-webkit-min-device-pixel-ratio:3) {
		.foods-info-js {
			border-bottom:0.3px solid rgba(7,17,27,0.1);
			border-top:0.3px solid rgba(7,17,27,0.1)
		}
	}
	.foods-info-js .foods-info-js-title{
		font-size: 0.28rem;
		color: rgb(7,17,27);
	}
	.foods-info-js .foods-info-js-cont{
		font-size: 0.24rem;
		font-weight: 200;
		color: rgb(77,85,93);
		line-height: 0.48rem;
		margin-top: 0.12rem;
	}
	.foods-ratings{
		background: #ffffff;
		border-top:1px solid rgba(7,17,27,0.1);
		width: 100%;
		padding-top: 0.36rem;
	}
	.foods-ratings .foods-ratings-title{
		font-size: 0.28rem;
		color: rgb(7,17,27);
		padding: 0 0.36rem 0.36rem 0.36rem;
	}
	.ratings-cont-list{
		padding: 0 0.36rem;
		border-top: 1px solid rgba(7,17,27,0.1);
	}
	.ratings-cont-list ul li{
		padding: 0.32rem 0;
		width: 100%;
		box-sizing: border-box;
		border-bottom: 1px solid rgba(7,17,27,0.1);
	}
	@media screen and (-webkit-min-device-pixel-ratio:2) {
		.ratings-cont-list ul li {
			border-bottom:0.5px solid rgba(7,17,27,0.1);
		}
	}
	
	@media screen and (-webkit-min-device-pixel-ratio:3) {
		.ratings-cont-list ul li {
			border-bottom:0.3px solid rgba(7,17,27,0.1);
		}
	}
	.ratings-cont-item-top{
		width: 100%;
		box-sizing: border-box;
		display: flex;
		flex-flow: ror nowrap;
		justify-content: space-between;
		align-items: center;
	}
	.ratings-cont-item-top .ratings-cont-item-time{
		font-size: 0.2rem;
		color: rgb(147,153,159);
		line-break: 0.24rem;
	}
	.ratings-cont-item-top .ratings-user-name{
		font-size: 0.2rem;
		color: rgb(147,153,159);
		line-height: 0.24rem;
	}
	.ratings-cont-item-top .ratings-user-name img{
		width: 0.24rem;
		height: 0.24rem;
		margin-left: 0.12rem;
		border-radius: 0.12rem;
	}
	.ratings-cont-list ul li .ratings-usr-text{
		font-size: 0.24rem;
		color: rgb(7,17,27);
		margin-top: 0.12rem;
	}
	.ratings-usr-text svg{
		width: 0.24rem;
		height: 0.24rem;
		margin-right: 0rem;
		vertical-align: middle;
	}
	.ratings-usr-text span{
		vertical-align: middle;
	}
	.ratings-usr-text svg.icon-good{
		color: rgb(0,160,220);
	}
	.ratings-usr-text svg.icon-nogood{
		color: rgb(147,153,159);
	}
</style>


