<template>
	<div id="app">
		<div class="flexd-conmponent">
			<div class="header-component">
				<v-header :seller="seller"></v-header>
			</div>
			<div class="nav-component">
				<ul>
					<li>
						<router-link to="/goods">商品</router-link>
					</li>
					<li>
						<router-link to="/ratings">评价</router-link>
					</li>
					<li>
						<router-link to="/seller">商家</router-link>
					</li>
					<!--<li>
						<router-link to="/icon">图标</router-link>
					</li>-->
				</ul>
			</div>
		</div>
		<router-view :seller="seller" :ratings="ratings" :goods="goods"></router-view>
	</div>
</template>

<script>
	import header from './components/header/header'

	const ERR_OK = 0;
	export default {
		name: 'app',
		data() {
			return {
				seller: {},
				ratings: {},
				goods:[],
			}
		},
		created() {
			this.$http.get('/eleme/api/seller').then(function(e) {
				let resource = e.body;
				if(resource.errno === ERR_OK) {
					this.seller = resource.data;
				}
			});
			this.$http.get('/eleme/api/ratings').then(function(e) {
				let ratings = e.body;
				if(ratings.errno === ERR_OK) {
					this.ratings = ratings.data;
				}
			});
			this.$http.get('/eleme/api/goods').then(function(res) {
				let goods = res.body
				if(goods.errno === ERR_OK) {
					this.goods = goods.data;
				}
			});
		},
		components: {
			"v-header": header,
		}
	}
</script>

<style>
	.flexd-conmponent {
		width: 100%;
	}
	
	.nav-component {
		background: rgb(255, 255, 255);
		position: relative;
	}
	
	.nav-component:after {
		position: absolute;
		bottom: 0;
		width: 100%;
		content: '';
		border-bottom: 1px solid #eeeeee;
	}
	
	@media screen and (-webkit-min-device-pixel-ratio: 2) {
		.nav-component:after {
			border-bottom: 0.5px solid #eeeeee;
		}
	}
	
	@media screen and (-webkit-min-device-pixel-ratio: 3) {
		.nav-component:after {
			border-bottom: 0.3px solid #eeeeee;
		}
	}
	
	.nav-component ul li a.router-link-active {
		font-weight: 700;
		color: #3190e8;
		position: relative;
	}
	
	.nav-component ul li a.router-link-active:after {
		position: absolute;
		bottom: -0.16rem;
		left: 0;
		background-color: #2395ff;
		height: 0.06rem;
		content: '';
		width: 100%;
	}
	
	.nav-component ul {
		display: flex;
		flex-direction: row;
	}
	
	.nav-component ul li {
		flex-grow: 1;
		font-size: 0.28rem;
		color: rgb(77, 85, 93);
		text-align: center;
		height: 0.82rem;
		line-height: 0.82rem;
	}
	
	.nav-component ul li a {}
	
	.nav-cont-component {}
	
	@import './common/style/reset.css'
</style>


