<template>
	<div class="ratings-box">
		<div class="ratings-select">
			<div class="ratings-select-btn" :class="{'active':selectType == 2}" @click="selectBtn(2)">
				{{selectText.allText}}<span>{{ratings.length}}</span>
			</div>
			<div class="ratings-select-btn" :class="{'active':selectType == 0}" @click="selectBtn(0)">
				{{selectText.tjText}}<span>{{tjRantingsLength}}</span>
			</div>
			<div class="ratings-select-btn" :class="{'active':selectType == 1}" @click="selectBtn(1)">
				{{selectText.btjText}}<span>{{btjRantingsLength}}</span>
			</div>
		</div>
		<div class="ratings-select-dg" :class="{'active':showContent}" @click="changeShowContent">
			 <svg class="icon" aria-hidden="true">
	            <use xlink:href="#icon-check_circle"></use>
	        </svg>
			<span>只看有内容的评价</span>
		</div>
		
	</div>
</template>

<script>
	const ALL = 2;
	const TUIJIAN = 0;
	const BUTUIJIAN = 1;
	export default{
		props:{
			ratings:{
				type:Array,
				default(){
					return []
				}
			},
			selectText:{
				type:Object,
				default:{
					allText:'全部',
					tjText:'推荐',
					btjText:'吐槽'
				}
			},
			showContent:{
				type:Boolean,
				default:true,
			},
			selectType:{
				type:Number,
				default:ALL,
			},
		},
		data(){
			return{
				
			}
		},
		methods:{
			selectBtn(index){
				this.$emit('selectBtnfu',index)
			},
			changeShowContent(){
				this.$emit('changeShowContentfu')
			}
		},
		computed:{
			tjRantingsLength(){
				return this.ratings.filter(function(rating){
					return rating.rateType == 0;
				}).length
			},
			btjRantingsLength(){
				return this.ratings.filter(function(rating){
					return rating.rateType == 1;
				}).length
			},
		}
	}
</script>

<style scoped>
	.ratings-select{
		display: flex;
		flex-direction: row;
		flex-wrap: nowrap;
		width: 6.78rem;
		padding-bottom: 0.36rem;
		border-bottom: rgba(7,17,27,0.1);
		margin: 0 auto;
		border-bottom:1px solid rgba(7,17,27,0.1);
	}
	.ratings-select-btn{
		font-size: 0.24rem;
		color: rgb(77,85,93);
		padding: 0.16rem 0.24rem;
		background-color: rgba(0,160,220,0.2);
		border-radius: 2px;
		margin-right: 0.16rem;
		color: rgb(77,85,93);
		
	}
	.ratings-select-btn.active{
		background-color: rgb(0,160,220);
		color: #ffffff;
	}
	.ratings-select-btn:nth-child(3){
		background-color: rgba(77,85,93,0.2);
	}
	.ratings-select-btn:nth-child(3).active{
		background-color: #555555;
		color: #ffffff;
	}
	@media screen and (-webkit-min-device-pixel-ratio:2) {
		.ratings-select {
			border-bottom:0.5px solid rgba(7,17,27,0.1);
		}
	}
	
	@media screen and (-webkit-min-device-pixel-ratio:3) {
		.ratings-select {
			border-bottom:0.3px solid rgba(7,17,27,0.1);
		}
	}
	.ratings-select-dg{
		font-size: 0.24rem;
		color: rgb(147,153,159);
		line-height: 0.48rem;
		padding: 0.24rem 0.36rem;
	}
	.ratings-select-dg svg{
		color: rgb(147,153,159);
		width: 0.48rem;
		height: 0.48rem;
		vertical-align: middle;
	}
	.ratings-select-dg.active svg{
		color:#00c850;
		width: 0.48rem;
		height: 0.48rem;
		vertical-align: middle;
	}
	.ratings-select-dg span{
		vertical-align: middle;
	}
	
</style>


