<template>
	<div>
		<div class="shop-cart-box">
			<div class="shop-cart-info">
				<div class="shop-cart" :class="{'shop-cart-on':nowShopsCount > 0}" @click="ShopCartsShow">
					<svg class="icon shop-cart-icon" aria-hidden="true">
			            <use xlink:href="#icon-shopping_cart"></use>
			       </svg>
			       <div class="now-shops-count" v-if="nowShopsCount > 0">{{nowShopsCount}}</div>
				</div>
				<div class="now-shop-price" :class="{'now-shop-price-on':nowAllPrice > 0}">
					￥{{nowAllPrice}}
				</div>
				<div class="more-shop-price">
					另需配送费￥{{seller.deliveryPrice}}元
				</div>
			</div>
			<div class="peisong-sprice" :class="{'peisong-sprice-on':nowGivePrice == '去结算'}">
				{{nowGivePrice}}
			</div>
		</div>
		<transition name="fade-in-bg">
			<div class="shop-carts-list" v-show="showList" @click="ShopCartsShow"></div>
		</transition>
		<transition name="shop-list-translate">
			<div class="shop-carts-cont" v-show="showList">
				<div class="shop-carts-cont-title">
					<div>购物车</div>
					<div class="clear-shops" @click="clearSelectShops">清空</div>
				</div>
				<div ref="shopsListBox" class="shops-list-box">
					<div>
						<div class="shop-carts-cont-list" v-for="item in selectShops">
							<div class="select-shops-name">{{item.name}}</div>
							<div class="select-shops-price">
								<span>￥</span>{{item.price * item.count}}
							</div>
							<div class="select-shops-count">
								<cartconcontrol :shops="item"></cartconcontrol>
							</div>
						</div>
					</div>
				</div>
			</div>
		</transition>
		<div class="ball-container">
			<div v-for="ball in balls">
				<!-- name="drop"-->  
				<transition name="drop" v-on:before-enter="beforeEnter" v-on:enter="enter" v-on:after-enter="afterEnter">
					<div class="ball" v-show="ball.show">
						<div class="inner inner-hook"></div>
					</div>
				</transition>
			</div>
		</div>
	</div>
	
</template>

<script>import cartconcontrol from '../cartconcontrol/cartconcontrol'
import BScroll from 'better-scroll'
export default {
	props: {
		selectShops: {
			type: Array,
			default: []
		},
		seller: {
			type: Object,
			default: {},
		}
	},
	created() {

	},
	data() {
		return {
			selectShopsListScroll: null,
			showShopsList: true,
			balls: [
				{
					show: false,
				}, 
				{
					show: false,
				},
				{
					show: false,
				},
				{
					show: false,
				},
				{
					show: false,
				}
			],
			dropBall:[]
		}
	},
	computed: {
		nowAllPrice() {
			let priceResult = 0;
			for(let i = 0; i < this.selectShops.length; i++) {
				priceResult += this.selectShops[i].price * this.selectShops[i].count
			}
			return priceResult;
		},
		nowGivePrice() {
			let nowGivePricetext = ``;
			let diffPrice = this.seller.minPrice - this.nowAllPrice
			if(diffPrice <= 0) {
				nowGivePricetext = `去结算`;
			} else {
				nowGivePricetext = `还差${diffPrice}起送`
			}
			return nowGivePricetext
		},
		nowShopsCount() {
			let shopsCountResult = 0;
			this.selectShops.forEach(function(shops) {
				shopsCountResult += shops.count
			})
			return shopsCountResult;
		},
		showList() {
			if(!this.nowShopsCount) {
				this.showShopsList = true;
				return false
			}
			let show = !this.showShopsList
			if(show) {
				this.$nextTick(() => {
					if(!this.scroll) {
						this.scroll = new BScroll(this.$refs.shopsListBox, {
							click: true,
						})
					} else {
						this.scroll.refresh()
					}

				})
			}
			return show
		}
	},
	watch: {
		nowShopsCount: function() {
			this.$emit('postNowShopsCount', this.nowShopsCount);
		},
	},
	methods: {
		ShopCartsShow() {
			if(!this.nowShopsCount) {
				return
			}
			this.showShopsList = !this.showShopsList;
		},
		clearSelectShops() {
			this.selectShops.forEach(function(goods) {
				goods.count = 0;
			})
		},
		drop(el){
			for(let i = 0; i < this.balls.length; i++){
				let ball = this.balls[i];
				if(!ball.show){
					ball.show= true;
					ball.el = el;
					this.dropBall.push(ball);
					return;
				}
			}
		},
		 beforeEnter: function (el) {
		    let count = this.balls.length;
		    while(count--){
		    	let ball = this.balls[count];
		    	if(ball.show){
		    		let rect = ball.el.getBoundingClientRect();
		    		let x = rect.left - 32;
		    		let y = -(window.innerHeight - rect.top - 22);
		    		el.style.display="";
		    		el.style.webkitTransform = `translateY(${y}px)`;
		    		el.style.transform = `translateY(${y}px)`;
		    		let inner = el.getElementsByClassName('inner-hook')[0];
		    		inner.style.webkitTransform = `translateX(${x}px)`;
		    		inner.style.transform = `translateX(${x}px)`;
		    	}
		    	
		    }
		 },
		 
		  enter: function (el) {
		    // ...
		    let rf = el.offsetHeight;
		    this.$nextTick(() => {
		    	el.style.webkitTransform = `translate3d(0,0,0)`;
	    		el.style.transform = `translate3d(0,0,0)`;
	    		let inner = el.getElementsByClassName('inner-hook')[0];
	    		inner.style.webkitTransform = `translate3d(0,0,0)`;
	    		inner.style.transform = `translate3d(0,0,0)`;
		    })
		    
		  },
		  afterEnter: function (el) {
		    // ...
		    let ball = this.dropBall.shift();
		    if(ball){
		    	ball.show = false;
		    	el.style.display="none";
		    }
		  },
	},
	components: {
		cartconcontrol,
	}

}</script>

<style scoped>.shop-cart-box {
	position: fixed;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 1rem;
	background: #141d27;
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	z-index: 99;
}

.shop-cart-box .shop-cart {
	position: absolute;
	left: 0.24rem;
	width: 0.98rem;
	height: 0.98rem;
	background: #2b343c;
	border-radius: 50%;
	border: 0.12rem solid #141d27;
	bottom: 0;
}

.shop-cart .shop-cart-icon {
	display: block;
	margin: 0.2rem auto;
	width: 0.55rem;
	height: 0.55rem;
	color: rgba(255, 255, 255, 0.4);
}

.shop-cart-info {
	flex-grow: 1;
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
}

.shop-cart-info .now-shop-price {
	font-size: 0.32rem;
	color: #919396;
	font-weight: 700;
	margin-left: 1.58rem;
	border-right: 1px solid rgba(255, 255, 255, 0.2);
	padding-right: 0.24rem;
	line-height: 0.48rem;
}

.shop-cart-info .now-shop-price.now-shop-price-on {
	color: #FFFFFF;
}

@media screen and (-webkit-min-device-pixel-ratio:2) {
	.shop-cart-info .now-shop-price {
		border-right: 0.5px solid rgba(255, 255, 255, 0.2);
	}
}

@media screen and (-webkit-min-device-pixel-ratio:3) {
	.shop-cart-info .now-shop-price {
		border-right: 0.3px solid rgba(255, 255, 255, 0.2);
	}
}

.shop-cart-info .more-shop-price {
	font-size: 0.24rem;
	color: #919396;
	padding-left: 0.24rem;
}

.peisong-sprice {
	flex-shrink: 0;
	width: 2.1rem;
	height: 1rem;
	line-height: 1rem;
	font-size: 0.25rem;
	color: #979a9c;
	font-weight: 700;
	background: #2b333b;
	text-align: center;
}

.peisong-sprice.peisong-sprice-on {
	background: #008736;
	color: #ffffff;
}

.now-shops-count {
	position: absolute;
	color: rgb(255, 255, 255);
	top: -0.11rem;
	font-size: 0.18rem;
	font-weight: 700;
	left: 0.6rem;
	background-color: rgb(240, 20, 20);
	box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.4);
	padding: 0rem 0.13rem;
	height: 0.34rem;
	line-height: 0.34rem;
	text-align: center;
	border-radius: 0.5rem;
}

.shop-cart-info .shop-cart-on {
	background-color: rgb(0, 160, 220);
}

.shop-cart-info .shop-cart-on .shop-cart-icon {
	color: #ffffff;
}

.shop-carts-list {
	position: fixed;
	top: 0;
	height: 100%;
	width: 100%;
	background: rgba(7, 17, 27, 0.6);
	z-index: 89;
}

.shop-carts-cont {
	position: absolute;
	bottom: 1rem;
	width: 100%;
	box-sizing: border-box;
	background: #ffffff;
	z-index: 90;
}

.shop-carts-cont-title,
.shop-carts-cont-list {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	flex-wrap: nowrap;
	align-items: center;
}

.shop-carts-cont-title {
	background: #f3f5f7;
	padding: 0 0.36rem;
	font-size: 0.28rem;
	font-weight: 200;
	color: rgb(7, 17, 27);
	line-height: 0.8rem;
	height: 0.8rem;
	border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}

@media screen and (-webkit-min-device-pixel-ratio:2) {
	.shop-carts-cont-title {
		border-bottom: 0.5px solid rgba(7, 17, 27, 0.1);
	}
}

@media screen and (-webkit-min-device-pixel-ratio:3) {
	.shop-carts-cont-title {
		border-bottom: 0.3px solid rgba(7, 17, 27, 0.1);
	}
}

.shop-carts-cont-title .clear-shops {
	color: rgb(0, 160, 220)
}

.shop-carts-cont-list {
	height: 0.96rem;
	line-height: 0.96rem;
	width: 6.78rem;
	margin: 0 auto;
	font-size: 0.28rem;
	border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}

@media screen and (-webkit-min-device-pixel-ratio:2) {
	.shop-carts-cont-list {
		border-bottom: 0.5px solid rgba(7, 17, 27, 0.1);
	}
}

@media screen and (-webkit-min-device-pixel-ratio:3) {
	.shop-carts-cont-list {
		border-bottom: 0.3px solid rgba(7, 17, 27, 0.1);
	}
}

.select-shops-name {
	width: 54%;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.select-shops-price {
	font-size: 0.28rem;
	font-weight: 700;
	color: rgb(240, 20, 20);
}

.select-shops-price span {
	font-size: 0.2rem;
}

.shops-list-box {
	overflow: hidden;
	max-height: 5.44rem;
}

.shop-list-translate-enter-active,
.shop-list-translate-leave-active {
	transition: all 0.35s ease;
}

.shop-list-translate-enter,
.shop-list-translate-leave-to {
	transform: translateY(100%);
}

.fade-in-bg-enter-active,
.fade-in-bg-leave-active {
	transition: all 0.2s linear;
}

.fade-in-bg-enter,
.fade-in-bg-leave-to {
	opacity: 0;
}
.ball-container .ball{
	position: fixed;
	left: 0.4rem;
	bottom: 0.3rem;
	z-index: 200;
}
.drop-enter-active{
	transition: all 0.3s cubic-bezier(0.49,-0.29,0.75,0.41);
	
}
.ball-container .inner{
	width: 0.28rem;
	height: 0.28rem;
	background: rgb(0,160,220);
	border-radius: 50%;
	transition: all 0.3s linear;
}
</style>


