<template>
	<div class="header">
		<div class="header-bg" :style="{background: 'url(' + seller.avatar + ') no-repeat'}"></div>
		<div class="header-cont">
			<div class="header-img">
				<img :src="seller.avatar"/>
			</div>
			<div class="header-info">
				<div class="header-info-brand-cont">
					<div class="header-info-brand"></div>
					<span>{{seller.name}}</span>
				</div>
				<div class="header-info-deliver">{{seller.description}}/{{seller.deliveryTime}}分钟送达</div>
				<div class="header-info-price" v-if="seller.supports" @click="detailShow">
					<div class="header-info-brand" :class="supportsIcon[seller.supports[0].type]"></div>
					<span>{{seller.supports[0].description}}</span>
					<div class="header-info-price-more">
						{{seller.supports.length}}个
							<svg class="icon arrow_right" aria-hidden="true">
					            <use xlink:href="#icon-keyboard_arrow_right"></use>
					       </svg>
					</div>
				</div>
			</div>
		</div>
		<div class="header-notice" @click="detailShow">
			<div class="header-notice-bg">{{seller.bulletin}}</div>
			<div class="header-notice-more">
				<svg class="icon arrow_right" aria-hidden="true">
		            <use xlink:href="#icon-keyboard_arrow_right"></use>
		       </svg>
			</div>
		</div>
		<transition name="slide-fade">
			<div class="header-detail clearfix" v-show="headerDetailShow" @touchmove="touchjs($event)">
				<div class="header-detail-main">
					<div class="header-detail-cont">
						<div class="header-detail-title">{{seller.name}}</div>
						<div class="header-star-box">
							<star-component :score="seller.score"></star-component>
						</div>
						
						<div class="header-detail-supports">
							<div class="header-detail-supports-line"></div>
							<div class="header-detail-supports-title">优惠信息</div>
							<div class="header-detail-supports-line"></div>
						</div>
						<div class="header-detail-supports-cont">
							<ul>
								<li v-for="item in seller.supports">
									<div class="header-info-brand" :class="supportsIcon[item.type]"></div>
									<span>{{item.description}}</span>
								</li>
							</ul>
						</div>
						<div class="header-detail-supports">
							<div class="header-detail-supports-line"></div>
							<div class="header-detail-supports-title">商家公告</div>
							<div class="header-detail-supports-line"></div>
						</div>
						<div class="header-detail-bulletin">
							{{seller.bulletin}}
						</div>
					</div>
				</div>
				<div class="header-detail-close">
					<svg class="icon icon-close" aria-hidden="true" @click="detailHide">
			            <use xlink:href="#icon-close"></use>
			        </svg>
				</div>
			</div>
		</transition>
	</div>
</template>
<script type="text/javascript">
import starComponent from '../star/star.vue'

export default{
	name:'header',
	props:{
		'seller':{
			type:Object,
		    default:{},
		},
	},
	data:function(){
		return {
			headerDetailShow:false,
		}
	},
	components:{
		'star-component':starComponent
	},
	created(){
		this.supportsIcon = ['decrease','discount','special','invoice','guarantee'];
		//this.a='decrease'
	},
	methods:{
		detailHide(){
			this.headerDetailShow = false;
		},
		detailShow(){
			this.headerDetailShow = true;
		},
		touchjs(e){
			var detailHeight = document.querySelector('.header-detail-main').offsetHeight;
			if(detailHeight <= document.documentElement.clientHeight){
				e.preventDefault()
			}
		}
	}
}
</script>
<style scoped>
	.header{
		width: 7.5rem;
		height: 2.7rem;
		position:relative;
		top: 0;
		left: 0;
		background: rgba(7,17,27,0.5);
		
	}
	.header-bg{
		width: 7.5rem;
		height: 2.7rem;
		position: absolute;
		top:0;
		left: 0;
		z-index: -1;
		filter: blur(7px);
		background-size:cover !important;
	}
	.header-cont{
		display: flex;
		flex-direction: row;
		flex-wrap: nowrap;
	}
	.header .header-img{
		width: 1.28rem;
		height: 1.28rem;
		margin: 0.5rem 0.3rem 0 0.5rem;
	}
	.header .header-img img{
		display: block;
		width: 1.28rem;
		height: 1.28rem;
		border-radius: 3px;
	}
	.header .header-info{
		margin-top: 0.5rem;
		flex-grow: 1;
		padding-right: 0.24rem;
	}
	.header-info-brand-cont .header-info-brand{
		background: url('../../common/img/brand@2x.png');
		width: 0.6rem;
		height: 0.36rem;
		background-size: 0.6rem 0.36rem;
		vertical-align: middle;
		display: inline-block;
		margin-right: 0.05rem;
		
	}
	.header-info-brand-cont span{
		vertical-align:middle;
	}
	.header-info-brand-cont{
		font-size: 0.32rem;
		color: rgb(255,255,255);
		font-weight: bold;
		margin-top: 0.04rem;
	}
	.header-info-deliver{
		font-size: 0.24rem;
		color: rgb(255,255,255);
		margin: 0.2rem 0 0.16rem 0;
	}
	.header-info-price{
		color: #ffffff;
		font-size: 0.2rem;
		position: relative;
	}
	.header-info-price .header-info-price-more{
		position: absolute;
		top: -0.14rem;
		right: 0;
		padding: 0.14rem 0.18rem;
		font-size: 0.2rem;
		color: rgb(255,255,255);
		background: rgba(0,0,0,0.2);
		border-radius: 0.25rem;
	}
	.header-info-price .header-info-price-more span{
		vertical-align: top;
	}
	.header-info-price span{
		vertical-align: middle;
	}
	.header-info-brand{
		display: inline-block;
		vertical-align: middle;
		width: 0.24rem;
		height: 0.24rem;
		background-size: 0.24rem 0.24rem;
	}
	.header-info-brand.decrease{
		background-image: url('../../common/img/decrease_2@2x.png');
	}
	.header-info-brand.discount{
		background-image: url('../../common/img/discount_2@2x.png');
	}
	.header-info-brand.guarantee{
		background-image: url('../../common/img/guarantee_2@2x.png');
	}
	.header-info-brand.invoice{
		background-image: url('../../common/img/invoice_2@2x.png');
	}
	.header-info-brand.special{
		background-image: url('../../common/img/special_2@2x.png');
	}
	.header-notice{
		height:0.56rem;
		line-height: 0.56rem;
		position: absolute;
		bottom: 0;
		background: rgba(7,17,27,0.2);
		width: 100%;
	}
	.header-notice-bg{
		font-size: 0.2rem;
		color: #ffffff;
		font-weight: 200;
		line-height: 0.56rem;
		padding-left: 0.76rem;
		box-sizing: border-box;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		padding-right: 0.5rem;
		background: url('../../common/img/bulletin@2x.png') no-repeat left 0.24rem center;
		background-size: 0.44rem 0.24rem;
	}
	.header-notice-more{
		font-size: 0.12rem;
		color: #ffffff;
		right: 0.24rem;
		top: 0;
		position: absolute;
	}
	.arrow_right{
		width: 0.24rem;
		height: 0.25rem;
		vertical-align: middle;
	}
	.header-detail{
		position: fixed;
		top: 0;
		background: rgba(7,17,27,0.9);
		width: 100%;
		height: 100%;
		z-index: 100;
		overflow: auto;
	}
	.header-detail-main{
		width: 100%;
		min-height: 100%;
	}
	.header-detail-cont{
		padding-top: 1.28rem;
		padding-bottom: 1.28rem;
	}
	.header-detail-close{
		position: relative;
		margin-top: -1.28rem;
	}
	.header-detail-close svg{
		display: block;
		margin: 0 auto;
	}
	.header-detail-close .icon-close{
		
		width: 0.64rem;
		height: 0.64rem;
		color: rgba(255,255,255,0.5);
	}
	.header-detail-title{
		font-size: 0.32rem;
		color: rgb(255,255,255);
		font-weight: 700;
		line-height: 0.32rem;
		text-align: center;
	}
	.header-detail-supports{
		width: 6.1rem;
		margin: 0.56rem auto 0.48rem auto;
		display: flex;
		flex-direction: row;
		justify-content: center;
		font-size: 0.28rem;
		font-weight: 700;
		color: rgb(255,255,255);
		align-items: center;
	}
	.header-detail-supports .header-detail-supports-line{
		background-color: rgba(255,255,255,0.2);
		height: 1px;
		flex-grow: 1;
		
	}
	.header-detail-supports .header-detail-supports-title{
		padding: 0 0.24rem;
	}
	.slide-fade-enter-active{
	    animation: bounce-in .3s;
	}
	.slide-fade-leave-active {
	    animation: bounce-in .2s reverse;
	}
	/*.slide-fade-enter ,.slide-fade-leave-to{
	  	opacity: 0;
	}*/
	@keyframes bounce-in {
	  0% {
	    transform: scale(0);
	    opacity: 0;
	    
	  }
	  50% {
	    opacity: 0;
	  }
	  100% {
	    transform: scale(1);
	  }
	}
	
	.header-detail-supports-cont{
		width: 6.1rem;
		margin: 0 auto;
		padding-left: 0.24rem;
		box-sizing: border-box;
	}
	.header-detail-supports-cont ul{
		
	}
	.header-detail-supports-cont ul li{
		font-size: 0.24rem;
		font-weight: 200;
		color: rgb(255,255,255);
		line-height: 0.24rem;
		margin-bottom: 0.26rem;

	}
	.header-detail-supports-cont ul li span{
		vertical-align: middle;
		margin-left: 0.06rem;
	}
	.header-detail-supports-cont .header-info-brand {
	    display: inline-block;
	    vertical-align: middle;
	    width: 0.32rem;
	    height: 0.32rem;
	    background-size: 0.32rem 0.32rem;
	}
	.header-detail-bulletin{
		width: 6.1rem;
		margin: 0 auto;
		color: rgb(255,255,255);
		font-weight: 200;
		line-height: 0.48rem;
		font-size: 0.24rem;
	}
	.header-star-box{
		margin: 0.32rem 0 0.56rem 0;
	}
</style>


