<template>
	<div class="header-detail-star" :class="{'minstar' : minstarstyle,'userminstar': userminstarstyle }">
		<ul>
			<li class="default" v-for="item in starscore" :class="item"></li>
		</ul>
	</div>
</template>

<script>
	const LENGTH = 5;
	const HALF = 'half';
	const DEFAULT = 'default';
	const LIGHT = 'light';
	
	export default{
		data(){
			return {
				
			}
		},
		props:{
			'score':{
				type:Number,
				default:0,
			},
			'minstarstyle':{
				type:Boolean,
				default:false,
			},
			'userminstarstyle':{
				type:Boolean,
				default:false,
			}
		},
		computed:{
			starscore:function(){
				let result = [];
				let rounding = Math.floor(this.score * 2) / 2;
				let remainder = rounding % 1 !== 0;
				let integer = Math.floor(rounding);
				for(let i = 0; i < integer; i++){
					result.push(LIGHT);
				}
				if(remainder){
					result.push(HALF);
				}
				while(result.length < LENGTH){
					result.push(DEFAULT);
				}
				return result;
			}
		}
	}
</script>

<style>
	.header-detail-star{
		
	}
	.header-detail-star ul{
		display: flex;
		flex-direction: row;
		justify-content: center;
	}
	.header-detail-star ul li{
		
		background-size:;
		width: 0.4rem;
		height: 0.38rem;
		background-size: 0.4rem 0.38rem;
		margin: 0 0.2rem;
	}
	.header-detail-star ul li.default{
		background-image:url('../../common/img/star48_off@3x.png');
	}
	.header-detail-star ul li.light{
		background-image:url('../../common/img/star48_on@3x.png');
	}
	.header-detail-star ul li.half{
		background-image:url('../../common/img/star48_half@2x.png');
	}
	.minstar.header-detail-star ul li{
		background-size:;
		width: 0.28rem;
		height: 0.26rem;
		background-size: 0.28rem 0.26rem;
		margin: 0rem 0.05rem;
	}
	.userminstar.header-detail-star ul li{
		background-size:;
		width: 0.26rem;
		height: 0.24rem;
		background-size: 0.26rem 0.24rem;
		margin:0 0.08rem 0 0;
	}
</style>


